@font-face {
  font-family: "Open Sans";
  src: url("../fonts/Open_Sans/OpenSans-Regular.ttf");
}

.App {
  height: 100vh;
  display: flex;
  background-color: rgb(26, 34, 44);
  color: rgb(180, 195, 211);
  font-family: "Open Sans";
  font-size: 13px;
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}